@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;700;900&display=swap);
*,
:after,
:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Colors */
  --main-color: #f9c301;
  --second-color: #fcdd2e;
  --violet-color: #a355d3;
  --green-color: #89c71f;
  --red-color: #eb4c54;
  --black-color: #000;
  --white-color: #fff;

  --shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

  /* Breakpoints */
  --mobile: 480px;
  --tablet: 768px;
  --desktop: 1200px;

  --to-tablet: 767px;
  --to-desktop: 1199px;
}

body {
  font-family: 'Nunito Sans', sans-serif;
  display: block;
  font-style: normal;
  line-height: 1.5;
  background-color: #fff;
  background-color: var(--white-color);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  display: block;
  padding: 0.5rem 1rem;
}

button:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  box-shadow: var(--shadow);
}

a:hover,
button:hover {
  cursor: pointer;
}

@media (max-width: 767px) {
  body{
   font-size: 14px;
  }
}
